export class Time {
    value;
    text;
    constructor(value, text) {
        this.value = value;
        this.text = text;
    }
}
export class Course {
    id;
    week;
    year;
    no;
    type;
    room_type;
    tutor;
    supp_tutor;
    groups;
    module;
    modulesupp;
    pay_module;
    constructor() {
        this.id = 0;
        this.week = '';
        this.year = '';
        this.no = 0;
        this.type = { department: { name: '' }, name: '' };
        this.room_type = { name: '' };
        this.tutor = '';
        this.supp_tutor = '';
        this.groups = [];
        this.module = { abbrev: '' };
        this.modulesupp = { abbrev: '' };
        this.pay_module = { abbrev: '' };
    }
}
export class ScheduledCourse {
    id;
    room;
    start_time;
    day;
    course;
    tutor;
    id_visio;
    constructor() {
        this.id = 0;
        this.room = 0;
        this.start_time = 0;
        this.day = '';
        this.course = {
            id: 0,
            type: '',
            room_type: '',
            week: 0,
            year: 0,
            groups: [
                {
                    id: 0,
                    train_prog: '',
                    name: '',
                    is_structural: false,
                },
            ],
            supp_tutor: [],
            module: {
                name: '',
                abbrev: '',
                display: {
                    color_bg: '',
                    color_txt: '',
                },
            },
            pay_module: {},
            is_graded: false,
        };
        this.tutor = '';
        this.id_visio = 0;
    }
}
